import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from '../api.service';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.css'],
  providers: [ ApiService ]
})
export class SigninComponent implements OnInit {

  email: string;
  password: string;

  constructor(private api: ApiService, private router: Router, public snack: MatSnackBar) { }

  ngOnInit() {
  }

  onSubmit(e): void {
    e.preventDefault();
    this.api.signin(this.email, this.password).subscribe(data => {
      if (data.ok === true) {
        window.localStorage.setItem('token', data.token);
        window.localStorage.setItem('account', data.account);
        this.router.navigate(['/']).then(done => {
          // skip since nothing happens
          this.snack.open(`Your private key is ${data.key} , this will disappear in 10 seconds, copy somewhere safe`, 'Copied!',{
            duration: 15000
          });
        }).catch(err => { console.log(err); });
      } else {
        alert(data.message);
      }
    });
  }

}
