import { Component, OnInit, Inject } from '@angular/core';
import { ApiService } from '../api.service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})

export class HomeComponent implements OnInit {

  balances: any[];
  assets: any[];

  constructor(private api: ApiService, private dialog: MatDialog, private snack: MatSnackBar) { }

  ngOnInit() {
    this.getBalances();
    this.getAssets();
  }

  getBalances() {
    this.api.viewAccountBalance(window.localStorage.getItem('account')).subscribe(data => {
      if (data.ok === true) {
        this.balances = data.balances;
        console.log(data);
      } else {
        alert(data.message);
      }
    });
  }

  getAssets() {
    this.api.getAssets().subscribe(data => {
      this.assets = data.data;
      console.log(data);
    });
  }

  authAccount() {
    const account = prompt('Account ID: ', '');
    const key = prompt('Private Key: ', '');

    this.api.authorizeAccount(key, account).subscribe(data => {
      if (data.ok === true) {
        this.snack.open('Successfully authorized, you can now make a transfer', 'OK', {
          duration: 3000
        });
      }
    });
  }

  acceptAsset(asset: any) {
    const key = prompt('Enter your Private Key: ', '');
    this.api.createTrustLine(key, asset.asset_code, asset.asset_issuer).subscribe(data => {
      if (data.ok === true) {
        this.snack.open('Success, you can now recieve these assets', 'ok', {
          duration: 3000
        });
        this.getBalances();
      }
    });
  }

  sendTokensDialog(): void {
    const dialogref = this.dialog.open(SendTokenComponent, {
      width: '300px'
    });
    dialogref.afterClosed().subscribe(info => {
      console.log(info);
    });
  }

  issueTokensDialog(): void {
    const dialogref = this.dialog.open(IssueIhubTokenComponent, {
      width: '300px'
    });
    dialogref.afterClosed().subscribe(info => {
      console.log(info);
    });
  }

  transactTokensDialog(): void {
    const dialogref = this.dialog.open(TransferIhubTokenComponent, {
      width: '300px'
    });
    dialogref.afterClosed().subscribe(info => {
      console.log(info);
    });
  }

}

// send tokens component
@Component({
  selector: 'app-send-tokens',
  templateUrl: 'send-tokens.html'
})

export class SendTokenComponent {

  reciever: String;
  amount: String;

  constructor(public dialogRef: MatDialogRef<SendTokenComponent>, @Inject(MAT_DIALOG_DATA) public data: any,
  private api: ApiService, private snack: MatSnackBar) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

  onSubmit(e): void {
    e.preventDefault();
    const key = prompt('Enter your private key: ', '');
    this.api.sendTokens(this.reciever, key, this.amount).subscribe(data => {
      console.log(data);
      if (data.ok === true) {
        this.dialogRef.close();
      } else {
        this.snack.open(data.message, 'OK', {
          duration: 3000
        });
        this.dialogRef.close();
      }
    });
  }
}

// issue new iHub tokens
@Component({
  selector: 'app-issue-tokens',
  templateUrl: 'issue-ihub-tokens.html'
})

export class IssueIhubTokenComponent {

  balance: String;
  assetBalance: String;
  email: String;
  password: String;
  private_key: String;

  constructor(public dialogRef: MatDialogRef<IssueIhubTokenComponent>, @Inject(MAT_DIALOG_DATA) public data: any,
  private api: ApiService, private snack: MatSnackBar) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

  onSubmit(e): void {
    e.preventDefault();
    this.api.issueIhubTokens(this.private_key, this.balance, this.assetBalance, this.email, this.password).subscribe(data => {
      if (data.ok === true) {
        this.dialogRef.close();
        this.snack.open(`Your Issuing account private key: ${data.data.key}.`, 'Copied!', {
          duration: 15000
        });
      } else {
        this.dialogRef.close();
      }
    });
  }
}

// transfer iHub tokens
@Component({
  selector: 'app-transfer-tokens',
  templateUrl: 'send-ihub-tokens.html'
})

export class TransferIhubTokenComponent {

  reciever: string;
  amount: string;
  issuer: String;

  constructor(public dialogRef: MatDialogRef<TransferIhubTokenComponent>, @Inject(MAT_DIALOG_DATA) public data: any,
  private api: ApiService, private snack: MatSnackBar) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

  onSubmit(e): void {
    e.preventDefault();
    const key = prompt('Enter your private key: ', '');
    this.api.sendIHubTokens(this.reciever, key, this.amount, this.issuer)
    .subscribe(data => {
      if (data.ok === true) {
        this.dialogRef.close();
      } else {
        this.snack.open(data.message, 'OK', {
          duration: 5000
        });
        this.dialogRef.close();
      }
    });
  }
}
