import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  host: String = '';

  constructor(private http: Http) { }

  login(email: String, password: String) {
    return this.http.post(`${this.host}/auth/login`, {
      email: email,
      password: password
    }).pipe(map(res => res.json()));
  }

  signin(email: String, password: String) {
    return this.http.post(`${this.host}/auth/signup`, {
      email: email,
      password: password
    }).pipe(map(res => res.json()));
  }

  sendTokens(reciever: String, privateKey: String, amount: String) {
    return this.http.post(`${this.host}/transact/send`, {
      reciever: reciever,
      private_key: privateKey,
      amount: amount
    }).pipe(map(res => res.json()));
  }

  sendIHubTokens(reciever: String, privateKey: String, amount: String, issuer: String) {
    return this.http.post(`${this.host}/transact/send_ihubtokens`, {
      reciever: reciever,
      private_key: privateKey,
      amount: amount,
      asset_issuer: issuer
    }).pipe(map(res => res.json()));
  }

  viewAccountBalance(accountId: String) {
    return this.http.get(`${this.host}/balance/${accountId}`).pipe(map(res => res.json()));
  }

  viewTransactionHistory(accountId: String) {
    return this.http.get(`${this.host}/transactions/${accountId}`).pipe(map(res => res.json()));
  }

  // Updated from /transact/send to /accounts/create_new_issuer
  issueIhubTokens(key: String, balance: String, assetBalance: String, email: String, password: String) {
    return this.http.post(`${this.host}/accounts/create_new_issuer`, {
      private_key: key,
      balance: balance,
      email: email,
      password: password,
      assetBalance: assetBalance
    }).pipe(map(res => res.json()));
  }

  // create a trustline
  createTrustLine(key: String, asset_code: String, asset_issuer: String) {
    return this.http.post(`${this.host}/accounts/create_trustline`, {
      private_key: key,
      asset_code: asset_code,
      asset_issuer: asset_issuer
    }).pipe(map(res => res.json()));
  }

  getAccountDetails(key: String) {
    return this.http.post(`${this.host}/accounts/get_details`, {
      private_key: key
    }).pipe(map(res => res.json()));
  }

  getAssets() {
    return this.http.get(`${this.host}/assets/assets`).pipe(map(res => res.json()));
  }

  authorizeAccount(key: String, account: String) {
    return this.http.post(`${this.host}/accounts/allow_trust`, {
      private_key: key,
      recipient: account
    }).pipe(map(res => res.json()));
  }

}
